nav {
  max-width: 1200px;
  padding: 2% 10%;
  margin: 0 auto;
  .wrapper {
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    a {
      text-decoration: none;
      color: black;
    }
    .active{
      font-weight: bold;
    }
  }

}


@media (min-width: 320px) and (max-width: 600px) {
  nav {
    .wrapper {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      a {
        padding-bottom: 5px;
      }
    }
  }
}